// File: src/utils/validation.js
import * as Yup from 'yup';

export const LoginValidationSchema = Yup.object().shape({
  email: Yup.string()
    .email('Email khng hp l')
    .required('Vui lng nhp email'),
  password: Yup.string()
    .min(8, 'Mt khu phi t nht 8 k t')
    .required('Vui lng nhp mt khu')
});

export const RegisterValidationSchema = Yup.object().shape({
  fullName: Yup.string()
    .required('Vui lng nhp h v tn'),
  email: Yup.string()
    .email('Email khng hp l')
    .required('Vui lng nhp email'),
  password: Yup.string()
    .min(8, 'Mt khu phi t nht 8 k t')
    .required('Vui lng nhp mt khu'),
  confirmPassword: Yup.string()
    .oneOf([Yup.ref('password'), null], 'Mt khu khng khp')
    .required('Vui lng xc nhn mt khu')
});

export const ResetPasswordSchema = Yup.object().shape({
  token: Yup.string()
    .required('M xc thc khng c  trng'),
  newPassword: Yup.string()
    .min(6, 'Mt khu phi c t nht 6 k t')
    .required('Vui lng nhp mt khu mi'),
  confirmPassword: Yup.string()
    .oneOf([Yup.ref('newPassword'), null], 'Mt khu xc nhn khng khp')
    .required('Vui lng xc nhn mt khu mi'),
});
