// File: src/services/bookService.js
import api from '../utils/api';

const bookService = {
  // Ly danh sch sch
  getAllBooks: async (params = {}) => {
    try {
      const response = await api.get('/books', { params });
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : new Error('Ly danh sch sch tht bi');
    }
  },

  // Ly chi tit sch
  getBookById: async (bookId) => {
    try {
      const response = await api.get(`/books/${bookId}`);
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : new Error('Ly chi tit sch tht bi');
    }
  },

  // Thm sch mi
  createBook: async (bookData) => {
    try {
      const response = await api.post('/books', bookData);
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : new Error('Thm sch mi tht bi');
    }
  },

  // Cp nht sch
  updateBook: async (bookId, bookData) => {
    try {
      const response = await api.put(`/books/${bookId}`, bookData);
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : new Error('Cp nht sch tht bi');
    }
  },

  // Xa sch
  deleteBook: async (bookId) => {
    try {
      const response = await api.delete(`/books/${bookId}`);
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : new Error('Xa sch tht bi');
    }
  },

  // Mn sch
  borrowBook: async (bookId) => {
    try {
      const response = await api.post(`/books/${bookId}/borrow`);
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : new Error('Mn sch tht bi');
    }
  },

  // Tr sch
  returnBook: async (bookId) => {
    try {
      const response = await api.post(`/books/${bookId}/return`);
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : new Error('Tr sch tht bi');
    }
  },

  // Tm kim sch
  searchBooks: async (query) => {
    try {
      const response = await api.get('/books/search', { 
        params: { query } 
      });
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : new Error('Tm kim sch tht bi');
    }
  }
};

export default bookService;